.header {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  height: 100vh;
  max-height: 850px;
  padding: 60px 315px 0 315px;
  background: linear-gradient(98.72deg, #010733 0%, rgba(1, 7, 51, 0.8) 48.11%, rgba(1, 7, 51, 0) 76.65%), url("/img/header-bg.jpg");
  overflow: hidden;
}
.header:before {
  content: "";
  position: absolute;
  width: 180px;
  height: 180px;
  filter: blur(10px);
  -o-filter: blur(10px);
  -ms-filter: blur(10px);
  -moz-filter: blur(10px);
  -webkit-filter: blur(10px);
  opacity: 0.7;
  top: 120px;
  left: -70px;
  background-size: cover;
  background-image: url("/img/header-ball.png");
}
.header__content {
  color: white;
  display: flex;
  justify-content: space-between;
  height: 100%;
}
.header__content .content__left .logo {
  display: flex;
  align-items: center;
  font-size: 26px;
  font-weight: bold;
  margin-bottom: 180px;
}
.header__content .content__left .logo img {
  margin-right: 7px;
}
.header__content .content__left .logo__text span {
  color: #245ff7;
}
.header__content .content__left .description h1 {
  font-size: 50px;
  line-height: 130%;
  font-weight: bold;
  margin-bottom: 40px;
}
.header__content .content__left .description p {
  font-weight: normal;
  font-size: 24px;
  line-height: 130%;
  margin-bottom: 60px;
  font-weight: 300;
}
.header__content .content__left .description button {
  background-color: #245ff7;
  border-radius: 6px;
  width: 260px;
  padding: 20px 30px;
  height: 59px;
  color: #ffffff;
  outline: none;
  border: none;
  cursor: pointer;
}
.header__content .content__left .description button:hover {
  background-color: #487bff;
}
.header__content .content__left .description button:active {
  background-color: #0e49df;
}
.header__content .content__right {
  display: flex;
  align-items: flex-end;
}