.questions {
  width: 100%;
  padding: 0 315px;
  max-width: 1920px;
  margin: 0 auto;
}
.questions__content {
  color: #333333;
  display: flex;
}
.questions__content .left {
  width: 630px;
  margin-right: 30px;
}
.questions__content .left h2 {
  font-size: 50px;
  line-height: 130%;
  font-weight: bold;
}
.questions__content .left span {
  color: #245ff7;
}
.questions__content .right .acor-container {
  margin: 0 0 20px 0;
}
.questions__content .right .acor-container .row__container {
  width: 630px;
}
.questions__content .right .acor-container .row__container label {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  z-index: 10;
  transition: color 0.35s;
  height: 62px;
}
.questions__content .right .acor-container .row__container label span {
  top: calc(50% - 11px);
  font-size: 18px;
  line-height: 120%;
}
.questions__content .right .acor-container .row__container label:after {
  content: "+";
  float: right;
}
.questions__content .right .acor-container .row__container input {
  display: none;
}
.questions__content .right .acor-container .row__container input:checked + label:after {
  transition: transform 0.15s;
  transform: rotate(45deg);
}
.questions__content .right .acor-container .row__container input:checked + label + .acor-body {
  height: auto;
  margin-top: -5px;
  color: #333333;
}
.questions__content .right .acor-container .row__container .acor-body {
  width: calc(100% - 40px);
  height: 0;
  color: rgba(0, 0, 0, 0);
  line-height: 18px;
  box-sizing: border-box;
  transition: color 0.5s, padding 0.5s;
  overflow: hidden;
  font-size: 16px;
}
.questions__content .right .acor-container .row__container .acor-body p {
  margin: 0 0 20px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 120%;
}