.what__will__you__know {
  background-color: #e8f1f9;
  position: relative;
  overflow: hidden;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 315px;
}
.what__will__you__know:before {
  content: "";
  position: absolute;
  width: 180px;
  height: 180px;
  filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  -moz-filter: blur(5px);
  -webkit-filter: blur(5px);
  opacity: 0.3;
  bottom: -55px;
  left: -30px;
  background-size: cover;
  background-color: #E8F1F9;
}
.what__will__you__know:after {
  content: "";
  position: absolute;
  width: 180px;
  height: 180px;
  filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  -moz-filter: blur(5px);
  -webkit-filter: blur(5px);
  opacity: 0.3;
  top: 15px;
  right: -70px;
  background-size: cover;
  background-color: #E8F1F9;
}
.what__will__you__know .what__will__you__know__header {
  margin-bottom: 170px;
  text-align: center;
}
.what__will__you__know .what__will__you__know__header h2 {
  font-size: 50px;
  line-height: 130%;
  font-weight: bold;
  margin-bottom: 20px;
}
.what__will__you__know .what__will__you__know__header h2 span {
  color: #245ff7;
}
.what__will__you__know .content {
  display: grid;
  grid-template-columns: repeat(3, 410px);
  grid-template-rows: 195px;
  grid-column-gap: 30px;
  grid-row-gap: 131px;
}
.what__will__you__know .content__block {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: flex-end;
  padding: 40px 30px;
  width: 410px;
  height: 195px;
  background: linear-gradient(102.62deg, #f8fcff 0%, #f1f9ff 100%);
  box-shadow: 0px 2px 20px rgba(15, 44, 72, 0.08);
}
.what__will__you__know .content__block p {
  text-align: center;
  font-size: 18px;
  line-height: 130%;
  font-weight: normal;
}
.what__will__you__know .content__block::after {
  position: absolute;
  content: "";
  width: 180px;
  height: 180px;
  bottom: 108px;
  left: 115px;
}
.what__will__you__know .content__block:nth-child(1):after {
  background: url("../../../img/what-will-know-img1.png ");
}
.what__will__you__know .content__block:nth-child(2):after {
  background: url("../../../img/what-will-know-img2.png ");
}
.what__will__you__know .content__block:nth-child(3):after {
  background: url("../../../img/what-will-know-img3.png ");
}
.what__will__you__know .content__block:nth-child(4):after {
  background: url("../../../img/what-will-know-img4.png");
}
.what__will__you__know .content__block:nth-child(5):after {
  background: url("../../../img/what-will-know-img5.png");
}
.what__will__you__know .content__block:nth-child(6):after {
  background: url("../../../img/what-will-know-img6.png");
}