.footer {
  flex: 0 0 auto;
  background-color: #333333;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 315px;
  font-weight: normal;
}
.footer .wrapper__footer {
  padding: 60px 0;
}
.footer .wrapper__footer ol li {
  margin-bottom: 10px;
}
.footer .wrapper__footer a {
  color: #ffffff;
  text-decoration: none;
  cursor: pointer;
}
.footer__content {
  display: flex;
  justify-content: space-between;
  color: #ffffff;
  font-size: 14px;
  line-height: 130%;
  font-weight: normal;
}
.footer__content .middle p {
  margin-bottom: 10px;
}
.footer__content .middle div {
  margin-bottom: 20px;
}
.footer__content .middle div img {
  margin-right: 10px;
}