.whats__learn {
  width: 100%;
  padding: 0 315px;
  max-width: 1920px;
  margin: 0 auto;
}
.whats__learn__content {
  display: flex;
}
.whats__learn__content .left {
  width: 520px;
  margin-right: 30px;
}
.whats__learn__content .left h2 {
  font-size: 50px;
  line-height: 130%;
  font-weight: bold;
}
.whats__learn__content .left span {
  color: #245ff7;
}
.whats__learn__content .right {
  width: 740px;
  font-size: 24px;
  font-weight: normal;
  line-height: 130%;
  color: #333333;
  padding-top: 10px;
}
.whats__learn__content .right h3 {
  font-size: 24px;
  line-height: 130%;
  color: #333333;
  font-weight: bold;
  margin-bottom: 15px;
}
.whats__learn__content .right p {
  font-size: 18px;
  line-height: 130%;
  font-weight: normal;
  margin-bottom: 30px;
}