.why__bets {
  width: 100%;
  padding: 0 315px;
  max-width: 1920px;
  margin: 0 auto;
}
.why__bets__content {
  display: flex;
}
.why__bets__content .content__left {
  width: 520px;
  margin-right: 30px;
}
.why__bets__content .content__left h2 {
  font-size: 50px;
  line-height: 130%;
  font-weight: bold;
}
.why__bets__content .content__left h2 span {
  color: #245ff7;
}
.why__bets__content .content__right {
  width: 740px;
  font-size: 24px;
  font-weight: normal;
  line-height: 130%;
  color: #333333;
  padding-top: 10px;
}
.why__bets__content .content__right p {
  margin-bottom: 25px;
}